<template>
  <v-container>
    <h2>Contact Management</h2> <!-- Main header -->

    <v-row>
      <v-col cols="12" md="6">
        <v-text-field v-model="projectFilter" label="Enter project name" outlined />
      </v-col>
      <v-col cols="12" md="6">
        <v-btn @click="loadContactsFromStrapi" color="primary">Load Data</v-btn>
      </v-col>
    </v-row>

    <v-alert v-if="loading" type="info" dismissible>
      Loading...
    </v-alert>

    <v-alert v-if="error" type="error" dismissible>
      An error occurred: {{ error.message }}
    </v-alert>

    <!-- Add Record button -->
    <v-btn @click="openAddForm" color="success" class="mb-4">Add Record</v-btn>

    <!-- Data table with alternate row colors -->
    <v-data-table
      v-if="!loading && !error && contacts.length && !currentContact && !isAdding"
      :headers="headers"
      :items="contacts"
      item-key="id"
      class="elevation-1"
      style="width: 100%"
    >
      <!-- Custom row rendering with alternating row colors -->
      <template v-slot:item="{ item, index }">
        <tr :class="getRowClass(index)">
          <td>{{ item.project }}</td>
          <td>{{ item.name }}</td>
          <td>{{ item.phoneNumber }}</td>
          <td>
            <v-btn @click="editContact(item)" color="primary" icon>
              <v-icon>mdi-pencil</v-icon>
            </v-btn>
          </td>
        </tr>
      </template>
    </v-data-table>

    <!-- Edit Contact Form -->
    <v-form v-if="currentContact && !isAdding" ref="contactForm">
      <v-row>
        <v-col cols="12" md="12">
          <h2>Edit Contact: {{ currentContact.name }}</h2>

          <v-textarea v-model="editContactData.interaction" label="Interaction" outlined></v-textarea>
          <v-text-field label="Doc" v-model="editContactData.doc" class="edit-mode"></v-text-field>
          <v-text-field label="Status" v-model="editContactData.status" class="edit-mode"></v-text-field>
          <v-text-field label="Rating" v-model="editContactData.rating" class="edit-mode"></v-text-field>

          <v-btn @click="saveContact" color="primary">Save</v-btn>
          <v-btn @click="cancelEdit" color="blue">Cancel</v-btn>
        </v-col>
      </v-row>
    </v-form>

    <!-- Add New Record Form -->
    <v-form v-if="isAdding" ref="addForm">
      <v-row>
        <v-col cols="12" md="12">
          <h2>Add New Contact</h2>

          <v-text-field v-model="newContactData.project" label="Project" outlined></v-text-field>
          <v-text-field v-model="newContactData.name" label="Name" outlined></v-text-field>
          <v-text-field v-model="newContactData.phoneNumber" label="Phone" outlined></v-text-field>
          <v-text-field v-model="newContactData.status" label="Status" outlined></v-text-field>
          <v-text-field v-model="newContactData.rating" label="Rating" outlined></v-text-field>

          <v-btn @click="saveNewContact" color="success">Save</v-btn>
          <v-btn @click="cancelAdd" color="blue">Cancel</v-btn>
        </v-col>
      </v-row>
    </v-form>
  </v-container>
</template>

<script>
import { gql } from '@apollo/client/core';
import { ref } from 'vue';
import client from '../apollo-client'; // Adjust the path as necessary

export default {
  name: 'listByProject',
  setup() {
    const projectFilter = ref(''); // Reactive project filter
    const contacts = ref([]); // Reactive contacts array
    const error = ref(null); // Reactive error message
    const loading = ref(false); // Reactive loading state
    const currentContact = ref(null); // Currently editing contact
    const editContactData = ref({}); // Data for editing contact
    const isAdding = ref(false); // Reactive flag to show Add form
    const newContactData = ref({}); // Data for adding new contact

    // Define the headers for the data table
    const headers = [
      { text: 'Project', title: 'Project', value: 'project', width: '15%' },
      { text: 'Name', title: 'Name', value: 'name', width: '15%' },
      { text: 'Phone', title: 'Phone', value: 'phoneNumber', width: '25%' },
      { text: 'Edit', title: 'Edit', value: 'edit', sortable: false, width: '10%' },
    ];

    // Function to open the form for adding a new contact
    function openAddForm() {
      isAdding.value = true;
      newContactData.value = {}; // Clear any previous data
    }

    function cancelAdd() {
      isAdding.value = false;
    }

   
    async function saveNewContact() {
      try {
        const addContactMutation = gql`
 mutation AddContact($name: String!, $project: String!, $email: String!,$package:String!,
  $phone: String!, $address: String!, $message: String!, $PreferredInstallationDate: String!, 
  $Nationality: String!, $ICFrontURL: String!, $ICBackURL: String!, $UtilityBillURL: String!) {
  createContact(data: { 
    project: $project, 
    package: $package,
    name: $name, 
    email: $email, 
    phone: $phone, 
    installationAddress: $address, 
    message: $message, 
    PreferredInstallationDate: $PreferredInstallationDate, 
    Nationality: $Nationality, 
    ICFrontURL: $ICFrontURL, 
    ICBackURL: $ICBackURL, 
    UtilityBillURL: $UtilityBillURL 
  }) {
    data {
      id
      attributes {
        project
        package
        name
        phone
        IC
        email
        installationAddress
        billingAddress
        message
        PreferredInstallationDate
        Nationality
        ICFrontURL
        ICBackURL
        UtilityBillURL 
      }
    }
  }
}
        `;

        const { data } = await client.mutate({
          mutation: addContactMutation,
          variables: { input: newContactData.value },
        });

        const newContact = {
          id: data.createLead.data.id,
          ...newContactData.value,
        };

        contacts.value.push(newContact);
        isAdding.value = false;
      } catch (err) {
        error.value = err;
        console.error('Error saving new contact:', err);
      }
    }

    async function loadContactsFromStrapi() {
      loading.value = true; // Set loading state
      contacts.value = []; // Clear existing contacts

      const fetchContactsQuery = gql`
        query($project: String!) {
          leads(filters: { project: { eq: $project } }, pagination: { limit: 1000 }) {
            data {
              id
              attributes {
                no
                project
                user
                name
                phoneNumber
                status
                phoneId
                interaction
                lastCall
                nextCall
                called
                rating
                dialedDate
                sourceDate
                contacted
                language
                message
                doc
                stage
                source
                occupation
                updatedAt
              }
            }
          }
        }
      `;

      try {
        const { data } = await client.query({
          query: fetchContactsQuery,
          variables: { project: projectFilter.value },
          fetchPolicy: 'no-cache',
        });

        data.leads.data.forEach((lead) => {
          const newContact = {
            id: lead.id,
            no: lead.attributes.no,
            user: lead.attributes.user || '',
            name: lead.attributes.name || '',
            phoneNumber: lead.attributes.phoneNumber || '',
            project: lead.attributes.project || '',
            occupation: lead.attributes.occupation || '',
            status: lead.attributes.status || '',
            phoneId: lead.attributes.phoneId || '',
            interaction: lead.attributes.interaction || '',
            lastCall: lead.attributes.lastCall || '1-1-1900',
            nextCall: lead.attributes.nextCall || '1-1-1900',
            called: lead.attributes.called || 0,
            rating: lead.attributes.rating || 0,
            dialedDate: lead.attributes.dialedDate || '1-1-1900',
            sourceDate: lead.attributes.sourceDate || '1-1-1900',
            contacted: lead.attributes.contacted || 0,
            language: lead.attributes.language || '',
            message: lead.attributes.message || '',
            doc: lead.attributes.doc || '',
            stage: lead.attributes.stage || '',
            source: lead.attributes.source || '',
            updatedAt: new Date().toISOString(),
          };

          contacts.value.push(newContact);
        });
      } catch (err) {
        error.value = err;
        console.error('Error loading contacts from Strapi:', err);
      } finally {
        loading.value = false; // Reset loading state
        projectFilter.value = ''; // Clear input field after loading
      }
    }

    function editContact(contact) {
      currentContact.value = contact;
      editContactData.value = { ...contact };
    }

    function cancelEdit() {
      currentContact.value = null;
    }

    async function saveContact() {
      const index = contacts.value.findIndex(c => c.id === currentContact.value.id);
      if (index !== -1) {
        contacts.value[index] = { ...contacts.value[index], ...editContactData.value };
      }
      currentContact.value = null;
    }

    // Method to alternate row colors
    const getRowClass = (index) => {
      return index % 2 === 0 ? 'even-row' : 'odd-row';
    };

    return {
      projectFilter,
      contacts,
      loadContactsFromStrapi,
      error,
      loading,
      headers,
      currentContact,
      editContactData,
      editContact,
      cancelEdit,
      saveContact,
      getRowClass,
      isAdding,
      newContactData,
      openAddForm,
      cancelAdd,
      saveNewContact,
    };
  },
};
</script>




<style scoped>
/* Define styles for alternating row colors */
.even-row {
  background-color: #f5f5f5; /* Light grey background for even rows */
}
.odd-row {
  background-color: #ffffff; /* White background for odd rows */
}
</style>
